import React from 'react';
import './Admin.css';
class AdminLogin extends React.Component{
	constructor(props){
		super(props);
		this.state={
			signInEmail:'',
			signInPassword:''
		}
	}
	onEmailChange = (event) => {
		this.setState({signInEmail:event.target.value})
	}
	onPasswordChange=(event)=>{
		this.setState({signInPassword:event.target.value})
	}
	onSubmitSignin=()=>{
				// if(this.signInEmail==='admin'&&this.signInPassword==='admin'){
				//  
				// }
				// else{
				// 	alert("Wrong Credentials");
				// }
				console.log(this.sigInEmail)
				this.props.onRouteChange('message');
			}
render(){
	const {onRouteChange}=this.props;
	return(
		<div className='admin'>
			<div className='abox'>
			<label className="db fw6 lh-copy f6 " htmlFor="email-address">Email</label>
 		        <input
 		         className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" 
		         type="email" 
 		         name="email-address"  
 		         id="email-address" 
 		         onChange={this.onEmailChange}
 		         />
 		        <label className="db fw6 lh-copy f6" htmlFor="password">Password</label>
 		        <input 
 		        className="b pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100"  		        type="password" 
 		        name="password"  
 		        id="password"
 		        onChange={this.onPasswordChange}/>
 		      <input className="b ph3 pv2 input-reset ba b--black bg-transparent grow pointer f6 dib" 
 		      onClick={this.onSubmitSignin}
 		      type="submit" 
		      value="Sign in"/>
</div>
		</div>
		);
}
}
export default AdminLogin;